@import url(https://fonts.googleapis.com/css?family=Lato|Open+Sans);
.overall {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.breadcrumb {
    margin-top: 24px;
    margin-left: 27.8px;
    list-style: none;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.breadcrumb .list-item {
    display: inline;
    font-size: 24px;
}

.breadcrumb .list-item+.list-item:before {
    padding: 8px;
    content: "/\A0";
}

.list-item:last-child {
    color: #000;
}
input:focus {
    outline: none;
}
.page-content {
    margin-top: 45px;
    margin-left: 24px;
    display: grid;
    grid-template-columns: repeat(4, 150px);
    grid-template-rows: 120px auto 120px;
    grid-column-gap: 20px;
    grid-row-gap: 65px;
    -webkit-align-items: stretch;
            align-items: stretch;
}

.grid-item {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.icon {
    margin: 12px 22px 9px 23px;
}

.icon-blue {
    margin: 12px 22px 9px 23px;
    padding-right: 12px;
    padding-left: 12px;
    background: #E6F5FF;
    border-radius: 8px;
}

.icon:hover {
    cursor: pointer;
}

.file-name {
    font-family: "Lato", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 16px;
    color: #535B62;
    letter-spacing: 0px;
}
.buttons {
    margin-top: 16px;
}

.file_blue {
    background: #4AB7FF;
}

.folder_blue {
    background: #4AB7FF;
}

.btn1 {
    background-color: "#fff";
    border: 1px solid #DDE0E4;
    background: none;
    box-shadow: none;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    outline: none;
    font-size: 16px;
    padding: 8px;
    float: left;
}

.btn2 {
    background-color: "#fff";
    border: 1px solid #DDE0E4;
    background: #4AB7FF;
    box-shadow: none;
    color: #fff;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    outline: none;
    font-size: 16px;
    padding: 8px;
}

.input:first-child {
    margin-top: 32px;
    color: #AFB2B6;
}

.input {
    margin-top: 16px;
    font-size: 14px;
    color: #AFB2B6;
    border: 1px solid #DDE0E4;
}

.input:nth-child(4) {
    margin-bottom: 24px;
}

.create {
    background: #4AB7FF;
    border-radius: 8px;
    color: #fff;
    font-size: 14px;
    width: 220px;
    height: 40px;
}

.inputs {
    margin-left: 27px;
}
.menu {
    height: 145px;
    width: 159px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 16px;
    color: #535B62;
    letter-spacing: 0;
    background: #FFFFFF;
    border: 1px solid rgba(221, 224, 228, 0.50);
    box-shadow: 0 12px 24px 0 rgba(0, 0, 0, 0.05);
    border-radius: 8px;
    z-index: 1000;
    margin-left: 200px;
}

.menu-text {
    padding-top: 15px;
    padding-left: 24px;
}

.option {
    background: #fff;
    /* height: 43px; */
    height: 48px;   /* Corrected for the gap that was present according to the design specs. */
    width: 100%;
}

.option:hover {
    cursor: pointer;
    background: #EEEFF1;
}

.option:nth-child(3) {
    color: #FF5942;
}
.content {
    float: right;
    width: calc(100% - 256px);
    background: #fff;
    height: 100vh;
}
.font-size-12 {
    font-family: "Lato", sans-serif;
    font-size: 24px;
    color: #AFB2B6;
    font-size: 12px;
    letter-spacing: 0;
    font-weight: bold;
    padding-left: 32px;
    padding-top: 32px;
    padding-bottom: 15px;
}

.sidebar {
    width: 256px;
    float: left;
    background: #F9FAFC;
    height: 100vh;
}

.sidebar-menu {
    width: 100%;
}

.sidebar-item {
    height: 43px;
    font-size: 16px;
    color: #2F363F;
    font-family: 'Lato', sans-serif;
    clear: both;
}

.inner-item {
    color: #535B62;
    clear: both;
}

.sidebar-item:active {
    background-color: #EEEFF1;
}

.sidebar-item-text {
    float: left;
    padding-left: 32px;
    padding-top: 12px;
}

.sidebar-item-inner-text {
    font-size: 16px;
    font-family: inherit;
    padding-left: 16px;
    padding-top: 12px;
    float: left;
}

.sidebar-item-extend {
    float: right;
    background-color: inherit;
    border: none;
    border-radius: 50%;
    width: 24px;
    height: 24px;
    margin-top: 9px;
    margin-right: 24px;
}

.vert-line {
    width: 2px;
    height: 16px;
    margin-top: 15px;
    margin-left: 32px;
    background: #EEEFF1;
    float: left;
}

.sidebar-item-extend:hover {
    cursor: pointer;
}

.sidebar-item-extend:focus {
    outline: 0;
}

.sidebar-item-extend .clicked {    /* TODO: Doesn't work as intended */
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    -webkit-transition-delay: 500ms;
            transition-delay: 500ms;
}
/* Also have to use Helvetica Neue */
.mainContent {
    width: 100%;
    position: relative;
}
